function debounce(fn, delay, options) {
    if(options && options.immediate === true){
        fn();
    }
    //  setTimeout
    var timer

    //  delay  fn 
    return function() {

        //  fn
        var context = this
        var args = arguments

        //  fn
        clearTimeout(timer)

        // 
        //  delay  fn
        timer = setTimeout(function() {
            fn.apply(context, args)
        }, delay)
    }
}
export default {
	debounce: debounce
}


